import React from "react";
// reactstrap components
import { Button, Modal } from "reactstrap";

function PortabilityModal(props) {
  return(
    <>
      <Modal isOpen={props.isOpen} toggle={props.toggle}>
        <div className="modal-header">
          <h5 className="modal-title" id="portabilityModalLongTitle">
            Portability
          </h5>
          <button
            aria-label="Close"
            className="close"
            data-dismiss="modal"
            type="button"
            onClick={props.toggle}
          >
            <span aria-hidden={true}>×</span>
          </button>
        </div>
        <div className="modal-body">
          <p>
              The portability of this application comes from a few different places.
              Firstly, the webapp can be run on any browser on any device supporting WASM.
              This gives devices ranging from computers to tablets to even phones the ability to use
              the Prime Number Generator.
          </p>
          <p>
            Regarding the native version, it is also designed with portability in mind. The application
            itself uses the NW.js library to port the webapp into a native application, and gives it access
            to built-in computer functionality like taskbar support and desktop notifications. While the application
            does not run in a browser, it can run in any environment where NW.js is supported, specifically on
            Windows, Mac, and Linux.
          </p>
        </div>
        <div className="modal-footer">
          <div className="left-side">
            <Button
              className="btn-link"
              color="default"
              data-dismiss="modal"
              type="button"
              onClick={props.toggle}
            >
              Close
            </Button>
          </div>
        </div>
      </Modal>
    </>
  );
}

export default PortabilityModal;