import React from "react";

// reactstrap components
import {
  Container,
  Row,
  Col
} from "reactstrap";

// core components
import SiteNavbar from "components/Navbars/SiteNavbar.js";
import SiteFooter from "components/Footers/SiteFooter.js";
import DownloadCard from "components/DownloadCard.js";

function DownloadPage() {
  const downloads = [
    {
      osName: "Windows",
      osDownload: "windows.zip"
    },
    {
      osName: "Mac",
      osDownload: "mac.zip"
    },
    {
      osName: "Linux",
      osDownload: "linux.zip"
    }
  ];

  let osIndex = 0;

  if (navigator.appVersion.indexOf("Win") != -1) {
    osIndex = 0;
  } else if (navigator.appVersion.indexOf("Mac") != -1) {
    osIndex = 1;
  } else if (navigator.appVersion.indexOf("Linux") != -1) {
    osIndex = 2;
  }

  return (
    <>
      <SiteNavbar disableScrollAnimation />
      <div className="main">
        <div className="section text-center">
          <Container>
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <h2 className="title">Native Application Download</h2>
                <p>To run, extract the downloaded zip file and follow the instructions in the Readme.txt file included within.</p>
              </Col>
            </Row>
            <br />
            <br />
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <div className="info">
                  <div className="icon icon-info">
                    <i className="nc-icon nc-cloud-download-93" />
                  </div>
                  <div className="description">
                    <h4 className="info-title">
                      Detected Operating System: {downloads[osIndex].osName} <br />
                      <small>Recommended Download</small>
                    </h4>
                    <p className="description">
                      <center><DownloadCard os={downloads[osIndex].osName} resource={downloads[osIndex].osDownload} recommended/></center>
                    </p>
                  </div>
                </div>
              </Col>
            </Row>
            <br />
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <div className="info">
                  <div className="icon icon-info">
                    <i className="nc-icon nc-globe" />
                  </div>
                  <div className="description">
                    <h4 className="info-title">Other Downloads</h4>
                  </div>
                </div>
              </Col>
            </Row>
            <Row>
              <Col className="ml-auto mr-auto" md="3">
                <div className="info">
                  <div className="description">
                    <p className="description">
                      <DownloadCard os={downloads[(osIndex + 1) % downloads.length].osName} resource={downloads[(osIndex + 1) % downloads.length].osDownload} />
                    </p>
                  </div>
                </div>
              </Col>
              <Col className="ml-auto mr-auto" md="3">
                <div className="info">
                  <div className="description">
                    <p className="description">
                    <DownloadCard os={downloads[(osIndex + 2) % downloads.length].osName} resource={downloads[(osIndex + 2) % downloads.length].osDownload} />
                    </p>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
      <SiteFooter />
    </>
  );
}

export default DownloadPage;
