import React from "react";

// reactstrap components
import {
  Button,
  Container,
  Row,
  Col,
  UncontrolledPopover,
  PopoverHeader,
  PopoverBody,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane
} from "reactstrap";

// core components
import SiteNavbar from "components/Navbars/SiteNavbar.js";
import SiteFooter from "components/Footers/SiteFooter.js";

function OverviewPage() {
  const [activeTab, setActiveTab] = React.useState("1");
  const toggle = (tab) => {
    if (activeTab !== tab) {
      setActiveTab(tab);
    }
  };
  return (
    <>
      <SiteNavbar disableScrollAnimation />
      <div className="main">
        <div className="section text-center">
          <Container>
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <h2 className="title">General Application Overview</h2>
              </Col>
            </Row>
            <br />
            <br />
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <div className="info">
                  <div className="icon icon-info">
                    <i className="nc-icon nc-laptop" />
                  </div>
                  <div className="description">
                    <h4 className="info-title">Application Environments</h4>
                    <p className="description">
                      This application can be run in the browser as
                      a webapp, or, on certain environments (Windows, Mac, Linux),
                      it can be downloaded and run as a native application. Both
                      environments provide the same base experience and full access
                      to all the prime number generators and their associated options,
                      but the native version extends the application with native functionality
                      like desktop notifications on execution completion and a taskbar menu
                      to quick-execute the program.
                    </p>
                  </div>
                </div>
              </Col>
            </Row>
            <br />
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <div className="info">
                  <div className="icon icon-info">
                    <i className="nc-icon nc-tile-56" />
                  </div>
                  <div className="description">
                    <h4 className="info-title">Generator Types</h4>
                    <p className="description">
                      There are several generator types available,
                      each of which can be used with any algorithm
                      and make different use of the configuration values
                      to return a specific result to the user.
                    </p>
                    <div className="nav-tabs-navigation">
                      <div className="nav-tabs-wrapper">
                        <Nav id="tabs" role="tablist" tabs>
                          <NavItem>
                            <NavLink
                              className={activeTab === "1" ? "active" : ""}
                              onClick={() => {
                                toggle("1");
                              }}
                            >
                              Between Values (Inclusive)
                            </NavLink>
                          </NavItem>
                          <NavItem>
                            <NavLink
                              className={activeTab === "2" ? "active" : ""}
                              onClick={() => {
                                toggle("2");
                              }}
                            >
                              Between Values (Exclusive)
                            </NavLink>
                          </NavItem>
                          <NavItem>
                            <NavLink
                              className={activeTab === "3" ? "active" : ""}
                              onClick={() => {
                                toggle("3");
                              }}
                            >
                              Between Values (Inclusive Min, Exclusive Max)
                            </NavLink>
                          </NavItem>
                          <NavItem>
                            <NavLink
                              className={activeTab === "4" ? "active" : ""}
                              onClick={() => {
                                toggle("4");
                              }}
                            >
                              Between Values (Exclusive Min, Inclusive Max)
                            </NavLink>
                          </NavItem>
                          <NavItem>
                            <NavLink
                              className={activeTab === "5" ? "active" : ""}
                              onClick={() => {
                                toggle("5");
                              }}
                            >
                              Less Than Value
                            </NavLink>
                          </NavItem>
                          <NavItem>
                            <NavLink
                              className={activeTab === "6" ? "active" : ""}
                              onClick={() => {
                                toggle("6");
                              }}
                            >
                              Less Than Or Equal To Value
                            </NavLink>
                          </NavItem>
                          <NavItem>
                            <NavLink
                              className={activeTab === "7" ? "active" : ""}
                              onClick={() => {
                                toggle("7");
                              }}
                            >
                              Validate Target Value
                            </NavLink>
                          </NavItem>
                        </Nav>
                      </div>
                    </div>
                    <TabContent activeTab={activeTab} className="text-center">
                      <TabPane tabId="1">
                        <p>
                          This generator uses a minimum and a maximum value to calculate
                          and return every prime number between the min and max, including
                          the min and max values if they are prime.
                        </p>
                      </TabPane>
                      <TabPane tabId="2">
                        <p>
                          This generator uses a minimum and a maximum value to calculate
                          and return every prime number between the min and max, not including
                          the min and max values themselves.
                        </p>
                      </TabPane>
                      <TabPane tabId="3">
                        <p>
                          This generator uses a minimum and a maximum value to calculate
                          and return every prime number between the min and max, including
                          the min value if it is prime.
                        </p>
                      </TabPane>
                      <TabPane tabId="4">
                        <p>
                          This generator uses a minimum and a maximum value to calculate
                          and return every prime number between the min and max, including
                          the max value if it is prime.
                        </p>
                      </TabPane>
                      <TabPane tabId="5">
                        <p>
                          This generator uses a maximum value to calculate and return
                          every prime number less than the specified max, not including
                          the max value.
                        </p>
                      </TabPane>
                      <TabPane tabId="6">
                        <p>
                          This generator uses a maximum value to calculate and return
                          every prime number less than the specified max, including the
                          max value if it is prime.
                        </p>
                      </TabPane>
                      <TabPane tabId="7">
                        <p>
                          This generator takes a user-inputted target value and checks
                          whether it is prime. It then returns whether or not the value
                          is prime.
                        </p>
                      </TabPane>
                    </TabContent>
                  </div>
                </div>
              </Col>
            </Row>
            <br />
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <div className="info">
                  <div className="icon icon-info">
                    <i className="nc-icon nc-settings" />
                  </div>
                  <div className="description">
                    <h4 className="info-title">Generator Options</h4>
                    <p className="description">
                      Several generator options are exposed via the
                      application panel, allowing users to tailor each
                      execution to their liking.
                    </p>
                    <Button
                      className="btn-round mr-1"
                      color="danger"
                      id="tooltipMinConfig"
                      outline
                      onClick={(e) => e.target.focus()}
                    >
                      Min
                    </Button>
                    <UncontrolledPopover
                      placement="bottom"
                      target="tooltipMinConfig"
                      trigger="focus"
                    >
                      <PopoverHeader>Min Value Config</PopoverHeader>
                      <PopoverBody>
                        The Min Value config allows the user to set the
                        minimum value in the range of numbers to be checked
                        for primeness. This configuration value is only used
                        in the Between Values generators. This value cannot
                        be larger than the Max Value.
                      </PopoverBody>
                    </UncontrolledPopover>
                    <Button
                      className="btn-round mr-1"
                      color="danger"
                      id="tooltipMaxConfig"
                      outline
                      onClick={(e) => e.target.focus()}
                    >
                      Max
                    </Button>
                    <UncontrolledPopover
                      placement="bottom"
                      target="tooltipMaxConfig"
                      trigger="focus"
                    >
                      <PopoverHeader>Max Value Config</PopoverHeader>
                      <PopoverBody>
                        The Max Value config allows the user to set the
                        maximum value in the range of numbers to be checked
                        for primeness. This configuration value is used in
                        the Between Values and Less Than Value generators.
                        This value cannot be smaller than the Min Value.
                      </PopoverBody>
                    </UncontrolledPopover>
                    <Button
                      className="btn-round mr-1"
                      color="danger"
                      id="tooltipTargetConfig"
                      outline
                      onClick={(e) => e.target.focus()}
                    >
                      Target
                    </Button>
                    <UncontrolledPopover
                      placement="bottom"
                      target="tooltipTargetConfig"
                      trigger="focus"
                    >
                      <PopoverHeader>Target Value Config</PopoverHeader>
                      <PopoverBody>
                        The Target Value config allows the user to set a
                        specific target integer to be checked for primeness.
                        This configuration value is only used in the Validate
                        generator.
                      </PopoverBody>
                    </UncontrolledPopover>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
      <SiteFooter />
    </>
  );
}

export default OverviewPage;
