import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Redirect, Switch } from "react-router-dom";

// styles
import "bootstrap/scss/bootstrap.scss";
import "assets/scss/paper-kit.scss?v=1.3.0";
// pages
import HomePage from "views/HomePage.js";
import OverviewPage from "views/OverviewPage.js";
import DocumentationPage from "views/DocumentationPage.js";
import DownloadPage from "views/DownloadPage.js";
import ScrollToTop from "components/ScrollToTop.js";
// others

ReactDOM.render(
  <BrowserRouter basename={process.env.PUBLIC_URL}>
    <ScrollToTop />
    <Switch>
      <Route
        path="/home"
        render={(props) => <HomePage {...props} />}
      />
      <Route
        path="/overview"
        render={(props) => <OverviewPage {...props} />}
      />
      <Route
        path="/docs"
        render={(props) => <DocumentationPage {...props} />}
      />
      <Route
        path="/download"
        render={(props) => <DownloadPage {...props} />}
      />
      <Route
        path="/webapp"
        render={(props) => window.location.href = "https://app.ameech7345.com"}
      />
      <Redirect to="/index" />
    </Switch>
  </BrowserRouter>,
  document.getElementById("root")
);
