import React from "react";
import { Link } from "react-router-dom";

// reactstrap components
import { Button, Container } from "reactstrap";

// core components

function HomePageHeader() {
  let pageHeader = React.createRef();

  React.useEffect(() => {
    if (window.innerWidth < 991) {
      const updateScroll = () => {
        let windowScrollTop = window.pageYOffset / 3;
        pageHeader.current.style.transform =
          "translate3d(0," + windowScrollTop + "px,0)";
      };
      window.addEventListener("scroll", updateScroll);
      return function cleanup() {
        window.removeEventListener("scroll", updateScroll);
      };
    }
  });

  return (
    <>
      <div
        style={{
          backgroundImage:
            "url(" + require("assets/img/home_prime_numbers.jpg").default + ")",
        }}
        className="page-header"
        data-parallax={true}
        ref={pageHeader}
      >
        <div className="filter" />
        <Container>
          <div className="motto text-center">
            <h1>Prime Number Generator</h1>
            <h3>Software developed for SMU CS 7345 by Alexander Meech</h3>
            <br />
            <Button
              to="/webapp"
              className="btn-round mr-1"
              color="neutral"
              target="_blank"
              outline
              tag={Link}
            >
              <i className="fa fa-play" />
              Preview in Browser
            </Button>
            <Button className="btn-round" color="neutral" outline to="/download" tag={Link}>
              <i className="fa fa-download" />
              Download Native Version
            </Button>
          </div>
        </Container>
      </div>
    </>
  );
}

export default HomePageHeader;
