import React from "react";
// reactstrap components
import { Button, Modal } from "reactstrap";

function PerformanceModal(props) {
  return(
    <>
      <Modal isOpen={props.isOpen} toggle={props.toggle}>
        <div className="modal-header">
          <h5 className="modal-title" id="performanceModalLongTitle">
            Performance Testing
          </h5>
          <button
            aria-label="Close"
            className="close"
            data-dismiss="modal"
            type="button"
            onClick={props.toggle}
          >
            <span aria-hidden={true}>×</span>
          </button>
        </div>
        <div className="modal-body">
          <p>
              Both the web and native versions of the application include
              a performance-testing mode, which allows the user to run tests
              of differing sizes (configurable) against each of the bundled
              algorithm APIs. These tests reply with timing data, which the discerning
              user is able to use to compare the relative performance of the webapp, the
              native app, and any other application with similar functionality to find the
              best fit for their use case.
          </p>
        </div>
        <div className="modal-footer">
          <div className="left-side">
            <Button
              className="btn-link"
              color="default"
              data-dismiss="modal"
              type="button"
              onClick={props.toggle}
            >
              Close
            </Button>
          </div>
        </div>
      </Modal>
    </>
  );
}

export default PerformanceModal;