import React from "react";
// reactstrap components
import { Button, Modal } from "reactstrap";

function ConfigurationModal(props) {
  return(
    <>
      <Modal isOpen={props.isOpen} toggle={props.toggle}>
        <div className="modal-header">
          <h5 className="modal-title" id="configurationModalLongTitle">
            Configuration Options
          </h5>
          <button
            aria-label="Close"
            className="close"
            data-dismiss="modal"
            type="button"
            onClick={props.toggle}
          >
            <span aria-hidden={true}>×</span>
          </button>
        </div>
        <div className="modal-body">
          <h6>API Configuration</h6>
          <p>
              The application can be configured to use the Sieve of Eratosthenes algorithm API, the
              Single Threaded Brute Force algorithm API, or the Multi Threaded Brute Force algorithm API.
              More information on these APIs can be found in the documentation.
          </p>
          <h6>Execution Configuration</h6>
          <p>
            The execution program can be configured with min, max, and target integers, which are used by
            the generator to determine what should be output back to the user. The generator can also be
            configured with an execution strategy. More information on the different strategies can be
            found in the overview.
          </p>
          <h6>Performance Test Configuration</h6>
          <p>
            The performance test can be configured with a test size, which allows the user to set the size
            of the test to be run for timing data.
          </p>
        </div>
        <div className="modal-footer">
          <div className="left-side">
            <Button
              className="btn-link"
              color="default"
              data-dismiss="modal"
              type="button"
              onClick={props.toggle}
            >
              Close
            </Button>
          </div>
        </div>
      </Modal>
    </>
  );
}

export default ConfigurationModal;