import React from "react";
import { Link } from "react-router-dom";

// reactstrap components
import {
  Button,
  Container,
  Row,
  Col,
} from "reactstrap";

// core components
import SiteNavbar from "components/Navbars/SiteNavbar.js";
import HomePageHeader from "components/Headers/HomePageHeader.js";
import SiteFooter from "components/Footers/SiteFooter.js";
import PortabilityModal from "components/Modals/PortabilityModal.js";
import ConfigurationModal from "components/Modals/ConfigurationModal.js";
import PerformanceModal from "components/Modals/PerformanceModal.js";
import LibraryModal from "components/Modals/LibraryModal.js";

function HomePage() {
  document.documentElement.classList.remove("nav-open");
  const [portabilityModalShown, setPortabilityModalShown] = React.useState(false);
  const [configurationModalShown, setConfigurationModalShown] = React.useState(false);
  const [performanceModalShown, setPerformanceModalShown] = React.useState(false);
  const [libraryModalShown, setLibraryModalShown] = React.useState(false);
  return (
    <>
      <SiteNavbar />
      <HomePageHeader />
      <div className="main">
        <PortabilityModal isOpen={portabilityModalShown} toggle={() => setPortabilityModalShown(false)} />
        <ConfigurationModal isOpen={configurationModalShown} toggle={() => setConfigurationModalShown(false)} />
        <PerformanceModal isOpen={performanceModalShown} toggle={() => setPerformanceModalShown(false)} />
        <LibraryModal isOpen={libraryModalShown} toggle={() => setLibraryModalShown(false)} />
        <div className="section text-center">
          <Container>
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <h2 className="title">Let's talk product</h2>
                <h5 className="description">
                  This program is designed to allow users to use their computers
                  to calculate groups of prime numbers or validate whether a given
                  number is prime. The application allows the user to select from
                  several predefined prime number generation algorithms, which can
                  also allow users to compare these algorithms against one another.
                  For more information about the application, visit the Detailed Overview
                  or view one of the information sections below.
                </h5>
                <br />
                <Button
                  className="btn-round"
                  color="info"
                  to="/overview"
                  tag={Link}
                >
                  Detailed Overview
                </Button>
              </Col>
            </Row>
            <br />
            <br />
            <Row>
              <Col md="3">
                <div className="info">
                  <div className="icon icon-info">
                    <i className="nc-icon nc-mobile" />
                  </div>
                  <div className="description">
                    <h4 className="info-title">Widely Portable</h4>
                    <p className="description">
                      You're not always working from your computer,
                      and now, through the power of WASM transpilation,
                      neither is this application!
                    </p>
                    <Button className="btn-link" color="info" type="button" onClick={() => setPortabilityModalShown(true)}>
                      See more
                    </Button>
                  </div>
                </div>
              </Col>
              <Col md="3">
                <div className="info">
                  <div className="icon icon-info">
                    <i className="nc-icon nc-settings-gear-65" />
                  </div>
                  <div className="description">
                    <h4 className="info-title">Powerful Configuration</h4>
                    <p>
                      Choose from multiple algorithms and concurrency strategies
                      to leverage the power of your device towards massive generator outputs.
                    </p>
                    <Button className="btn-link" color="info" type="button" onClick={() => setConfigurationModalShown(true)}>
                      See more
                    </Button>
                  </div>
                </div>
              </Col>
              <Col md="3">
                <div className="info">
                  <div className="icon icon-info">
                    <i className="nc-icon nc-chart-bar-32" />
                  </div>
                  <div className="description">
                    <h4 className="info-title">Performance Testing</h4>
                    <p>
                      Use built-in performance testing to compare the webapp to the native,
                      and compare this software against any other!
                    </p>
                    <Button className="btn-link" color="info" type="button" onClick={() => setPerformanceModalShown(true)}>
                      See more
                    </Button>
                  </div>
                </div>
              </Col>
              <Col md="3">
                <div className="info">
                  <div className="icon icon-info">
                    <i className="nc-icon nc-atom" />
                  </div>
                  <div className="description">
                    <h4 className="info-title">Industry-Standard Libraries</h4>
                    <p>
                      Production-ready libraries are used to make this app efficient
                      and effective for any task, in any environment.
                    </p>
                    <Button className="btn-link" color="info" type="button" onClick={() => setLibraryModalShown(true)}>
                      See more
                    </Button>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
      <SiteFooter />
    </>
  );
}

export default HomePage;
