import React from "react";
// reactstrap components
import { Button, Modal } from "reactstrap";

function LibraryModal(props) {
  return(
    <>
      <Modal isOpen={props.isOpen} toggle={props.toggle}>
        <div className="modal-header">
          <h5 className="modal-title" id="libraryModalLongTitle">
            Libraries in Use
          </h5>
          <button
            aria-label="Close"
            className="close"
            data-dismiss="modal"
            type="button"
            onClick={props.toggle}
          >
            <span aria-hidden={true}>×</span>
          </button>
        </div>
        <div className="modal-body">
          <h6>Emscripten</h6>
          <p>
              The Emscripten toolchain is in use to transpile the C++ prime number generator API
              into a portable WASM and Javascript program which can be used in the webapp and the NW.js
              application. More information about Emscripten can be found <a href="https://emscripten.org/" target="_blank" rel="noreferrer">here</a>
          </p>
          <h6>NW.js</h6>
          <p>
            The NW.js toolchain was used to build the native version of the application. It allows the
            application to use native features like desktop notifications and taskbar menus. More information
            about NW.js can be found <a href="https://nwjs.io/" target="_blank" rel="noreferrer">here</a>
          </p>
          <h6>React</h6>
          <p>
            The React library was used to build this website for the application, containing documentation
            and other information! More information about React can be found <a href="https://reactjs.org/" target="_blank" rel="noreferrer">here</a>
          </p>
        </div>
        <div className="modal-footer">
          <div className="left-side">
            <Button
              className="btn-link"
              color="default"
              data-dismiss="modal"
              type="button"
              onClick={props.toggle}
            >
              Close
            </Button>
          </div>
        </div>
      </Modal>
    </>
  );
}

export default LibraryModal;