import React from "react";
import { Link } from "react-router-dom";

// reactstrap components
import {
    Card,
    Button
  } from "reactstrap";

function DownloadCard(props) {
    return (
        <Card className="card-download">
            <h3 className="card-title mx-auto">{props.os}</h3>
            <Button
                block
                className="btn-round"
                color={props.recommended ? "success" : "info"}
                type="button"
                href={"https://app.ameech7345.com/downloads/" + props.resource}
                target="_blank"
            >
                Download
            </Button>
        </Card>
    );
}

export default DownloadCard;