import React from "react";

// reactstrap components
import {
  Container,
  Row,
  Col
} from "reactstrap";

// core components
import SiteNavbar from "components/Navbars/SiteNavbar.js";
import SiteFooter from "components/Footers/SiteFooter.js";

function DocumentationPage() {
  return (
    <>
      <SiteNavbar disableScrollAnimation />
      <div className="main">
        <div className="section text-center">
          <Container>
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <h2 className="title">Developer Documentation</h2>
              </Col>
            </Row>
            <br />
            <br />
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <div className="info">
                  <div className="icon icon-info">
                    <i className="nc-icon nc-caps-small" />
                  </div>
                  <div className="description">
                    <h4 className="info-title">API Documentation</h4>
                    <br />
                    <h7>PrimeNumberGeneratorType</h7>
                    <p className="description">
                      <ul>
                        <li>NONE: Default value for an uninitialized set of options, does not generate any numbers.</li>
                        <li>BETWEEN_VALUES_INCLUSIVE: This generator type computes and outputs all prime numbers which fall between a specified minimum and maximum 64-bit integer value. This type has a range which includes the specified minimum and maximum, meaning a prime number equal to the minimum or maximum will be included in the output. This generator type assumes that a minimum and maximum will be provided to the algorithm via the generator options specified by the user.</li>
                        <li>BETWEEN_VALUES_EXCLUSIVE: This generator type computes and outputs all prime numbers which fall between a specified minimum and maximum 64-bit integer value. This type has a range which excludes the specified minimum and maximum, meaning a prime number equal to the minimum or maximum will not be included in the output. This generator type assumes that a minimum and maximum will be provided to the algorithm via the generator options specified by the user.</li>
                        <li>BETWEEN_VALUES_INCLUSIVE_EXCLUSIVE: This generator type computes and outputs all prime numbers which fall between a specified minimum and maximum 64-bit integer value. This type has a range which includes the specified minimum and excludes the specified maximum, meaning a prime number equal to the minimum will be included and a prime number equal to the maximum will not be included in the output. This generator type assumes that a minimum and maximum will be provided to the algorithm via the generator options specified by the user.</li>
                        <li>BETWEEN_VALUES_EXCLUSIVE_INCLUSIVE: This generator type computes and outputs all prime numbers which fall between a specified minimum and maximum 64-bit integer value. This type has a range which excludes the specified minimum and includes the specified maximum, meaning a prime number equal to the minimum will not be included and a prime number equal to the maximum will be included in the output. This generator type assumes that a minimum and maximum will be provided to the algorithm via the generator options specified by the user.</li>
                        <li>LESS_THAN_VALUE: This generator type computes and outputs all positive prime numbers less than a specified maximum 64-bit integer value. If a prime number is equal to the maximum, it will not be included in the output. This generator type assumes that a maximum will be provided to the algorithm via the generator options specified by the user.</li>
                        <li>LESS_THAN_OR_EQUAL_TO_VALUE: This generator type computes and outputs all positive prime numbers less than or equal to a specified maximum 64-bit integer value. If a prime number is equal to the maximum, it will be included in the output. This generator type assumes that a maximum will be provided to the algorithm via the generator options specified by the user.</li>
                        <li>VALIDATE: This generator type validates whether a specific 64-bit integer value is prime or not. If the value is prime, the total generated primes will be of length 1 and will contain the value at index 0. If the value is not prime, the total generated primes will be of length 0 and will contain no values. This generator type assumes that a target will be provided to the algorithm via the generator options specified by the user.</li>
                      </ul>
                    </p>
                    <br />
                    <h7>PrimeNumberGeneratorOptions</h7>
                    <p className="description">
                      <ul>
                        <li>A new instance of this object can be created for each API use, or one instance can be retained and updated for each new use. Updates to this object will not propagate to any API instances it was used to customize.</li>
                        <li>Each setter method in this class can be chained together in sequence to update multiple options at once.</li>
                        <li>getGeneratorType(): This method returns the current specified generator type for this set of options. By default, this value will be NONE.</li>
                        <li>setGeneratorType(): This method accepts a generator type to be applied to this set of options. Changing generator type will not update any other options.</li>
                        <li>getMin(): This method returns the current specified 64-bit integer minimum value.</li>
                        <li>setMin(): This method accepts a new 64-bit integer minimum value. It will only be used by certain generator types (more info can be found in the enum description above).</li>
                        <li>getMax(): This method returns the current specified 64-bit integer maximum value.</li>
                        <li>setMax(): This method accepts a new 64-bit integer maximum value. It will only be used by certain generator types (more info can be found in the enum description above).</li>
                        <li>getTarget(): This method returns the current specified 64-bit integer target value.</li>
                        <li>setTarget(): This method accepts a new 64-bit integer target value. It will only be used by certain generator types (more info can be found in the enum description above).</li>
                      </ul>
                    </p>
                    <br />
                    <h7>PrimeNumberGeneratorResult</h7>
                    <p className="description">
                      <ul>
                        <li>This object should never be instantiated by the user, it will only be created and updated by an active API instance.</li>
                        <li>An instance of this object will be modified by each subsequent API execution, so any needed data should be copied out of it before executing again.</li>
                        <li>getTotalGenerated(): This method returns a 64-bit integer value representing the total number of primes computed by the last API execution. If no primes were computed, the value will be 0.</li>
                        <li>getGeneratedPrime(): This method accepts a 64-bit integer representing an array index to access the generated 64-bit prime number at that location. This method assumes the index to be between 0 (inclusive) and the value returned by getTotalGenerated() (exclusive).</li>
                        <li>getGeneratedPrimes(): This method returns a pointer representing an array of 64-bit integers, each of which is a prime number computed by the last API execution. The length of the array is returned by getTotalGenerated(). The pointer is const, so the array cannot be modified or deleted, any such modification should be done on a copy of the data. If the total number of generated primes is 0, this method will return nullptr.</li>
                      </ul>
                    </p>
                    <br />
                    <h7>PrimeNumberAPI</h7>
                    <p className="description">
                      <ul>
                        <li>This is the base class for any API implementation, containing functionality for specifying generator options, retrieving generator results, and for performing timing analysis on API execution.</li>
                        <li>This is an abstract class, with the actual API call being a pure virtual function. This class should never be used on its own, but should be inherited by another class containing an API algorithm implementation utilizing the base functionality provided by this class.</li>
                        <li>setGeneratorOptions(): This method accepts an instance of the PrimeNumberGeneratorOptions class described above. The options within that instance will be copied over to the API instance’s state, so subsequent changes to the option instance will not affect the API instance unintentionally. This method assumes that the provided options will be sufficient to execute any available generator algorithm.</li>
                        <li>execute(): This method starts a timer and makes a call to the virtual generator method to invoke the algorithm provided by the inheriting class. After the generator completes, the timer is stopped and the method returns the total elapsed milliseconds. Calling this method again will wipe the reference to the previous execution’s results.</li>
                        <li>getGeneratorResult(): This method returns a reference to a PrimeNumberGeneratorResult object as defined above, containing the results from the last execution of this API instance. If an execution has not yet occurred, the result instance will contain an empty array of primes. The returned reference is read-only, and should only be used for outputting or copying data.</li>
                        <li>reset(): This method resets the options and results for this API instance back to their defaults, which does not execute any generator functionality. Calling this method will free the memory containing the last API execution results.</li>
                      </ul>
                    </p>
                    <br />
                    <h7>SievePrimeNumberAPI</h7>
                    <p className="description">
                      <ul>
                        <li>This class provides a single-threaded implementation of the Sieve of Eratosthenes algorithm to generate prime numbers based on the options specified in the base class.</li>
                        <li>The algorithm-specific methods in this class should not be called directly by the user, only by the API internals.</li>
                        <li>This class inherits from the PrimeNumberAPI defined above.</li>
                      </ul>
                    </p>
                    <br />
                    <h7>SingleThreadPrimeNumberAPI</h7>
                    <p className="description">
                      <ul>
                        <li>This class provides a single-threaded implementation of the brute force algorithm to generate prime numbers based on the options specified in the base class.</li>
                        <li>The algorithm-specific methods in this class should not be called directly by the user, only by the API internals.</li>
                        <li>This class inherits from the PrimeNumberAPI defined above.</li>
                      </ul>
                    </p>
                    <br />
                    <h7>MultiThreadNumberAPI</h7>
                    <p className="description">
                      <ul>
                        <li>This class provides a multi-threaded implementation of the brute force algorithm to generate prime numbers based on the options specified in the base class.</li>
                        <li>The algorithm-specific methods in this class should not be called directly by the user, only by the API internals.</li>
                        <li>This class inherits from the PrimeNumberAPI defined above.</li>
                      </ul>
                    </p>
                  </div>
                </div>
              </Col>
            </Row>
            <br />
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <div className="info">
                  <div className="icon icon-info">
                    <i className="nc-icon nc-vector" />
                  </div>
                  <div className="description">
                    <h4 className="info-title">Multithreading Architecture and Execution Model</h4>
                    <p className="description">
                      In the MultiThreadPrimeNumberAPI, the brute force algorithm for prime numbers
                      is parallelized and executed across multiple threads. During the algorithm setup,
                      the API creates a thread pool of up to 5 threads. If the total number of primes
                      needed is less than 5, that number of threads is allocated instead. Each of these
                      newly allocated threads is passed a starting value to check, the maximum value needing
                      to be checked, and an offset amount to move from one value to the next. On the main
                      thread, a hash set is allocated to store all the generated prime numbers so that
                      any value can be checked against that container in constant time. A mutex is also
                      created in order to lock access to the container and give one thread exclusive
                      read/write access. A reference to this container and mutex are also passed to the
                      new threads. The threads then begin to check each value in their assigned range to
                      determine whether it is prime, acquiring write access to the container and pushing
                      it if the value is prime. The thread then moves on to the next value by adding the
                      predetermined offset to the current value. In this manner, each thread can be reused
                      for each remaining value, avoiding many expensive thread initialization/termination
                      calls. The write lock acquired on the mutex is also restricted to the portion of the
                      thread code that modifies the container rather than the entire iteration, minimizing
                      lock times and increasing the lock availability to each of the other threads. The lock
                      ensures that the state of the container remains consistent across all of the threads
                      and that no race conditions occur. After initiating the algorithm, the main thread
                      blocks until each thread in the pool finishes executing, then destroys the pool and
                      reports all the results back to the API’s user.
                    </p>
                  </div>
                </div>
              </Col>
            </Row>
            <br />
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <div className="info">
                  <div className="icon icon-info">
                    <i className="nc-icon nc-image" />
                  </div>
                  <div className="description">
                    <h4 className="info-title">UML Diagram</h4>
                    <p className="description">
                      <img
                        alt="API UML Diagram"
                        className="img-rounded img-responsive"
                        src={require("assets/img/uml_diagram.png").default}
                      />
                    </p>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
      <SiteFooter />
    </>
  );
}

export default DocumentationPage;
